.services-section {
  padding: 100px 7.6923%;
  background: var(--bg-primary);
  position: relative;
}

.services-container {
  max-width: 1400px;
  margin: 0 auto;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
  gap: 40px;
  margin-top: 60px;
}

.service-card {
  background: var(--bg-secondary);
  border: 1px solid var(--border-subtle);
  overflow: hidden;
  transition: all 0.4s ease;
  animation: fadeInUp 0.8s ease-out forwards;
  opacity: 0;
  position: relative;
}

.service-card:hover {
  transform: translateY(-8px);
  border-color: var(--brand-primary);
  box-shadow: 0 20px 60px rgba(0, 191, 255, 0.3);
}

.service-image-container {
  position: relative;
  width: 100%;
  height: 250px;
  overflow: hidden;
}

.service-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.6s ease;
}

.service-card:hover .service-image {
  transform: scale(1.1);
}

.service-image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(to top, var(--bg-secondary), rgba(11, 11, 11, 0.4));
}

.service-icon {
  position: absolute;
  top: 20px;
  right: 20px;
  background: rgba(0, 191, 255, 0.2);
  backdrop-filter: blur(12px);
  border: 1px solid var(--brand-primary);
  padding: 12px;
  color: var(--brand-primary);
  z-index: 2;
  transition: all 0.4s ease;
}

.service-card:hover .service-icon {
  background: var(--brand-primary);
  color: #000000;
  transform: rotate(10deg) scale(1.1);
}

.service-content {
  padding: 24px;
}

.service-title {
  font-size: 24px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 12px;
  letter-spacing: -0.5px;
}

.service-description {
  font-size: 16px;
  line-height: 1.6;
  color: var(--text-secondary);
  margin-bottom: 20px;
}

.service-features {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.service-feature {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 14px;
  color: var(--text-secondary);
}

.feature-dot {
  width: 6px;
  height: 6px;
  background: var(--brand-primary);
  flex-shrink: 0;
}

@media (max-width: 768px) {
  .services-section {
    padding: 60px 20px;
  }
  
  .services-grid {
    grid-template-columns: 1fr;
  }
  
  .service-image-container {
    height: 200px;
  }
}