:root {
  /* Backgrounds - Dark Theme Foundation */
  --bg-primary: #0b0b0b;
  --bg-secondary: #121212;
  --bg-overlay: rgba(255, 255, 255, 0.1);
  
  /* Text - High Contrast for Dark Theme */
  --text-primary: #ffffff;
  --text-secondary: rgba(255, 255, 255, 0.85);
  --text-muted: #4d4d4d;
  
  /* Borders - Subtle Dark Theme */
  --border-subtle: rgba(255, 255, 255, 0.25);
  --border-medium: rgba(255, 255, 255, 0.4);
  
  /* MAIN ACCENT COLORS - Brand Colors */
  --brand-primary: #00bfff;
  --brand-hover: rgba(0, 191, 255, 0.1);
  --brand-active: #6FD2C0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: var(--bg-primary);
  color: var(--text-primary);
  overflow-x: hidden;
}

.App {
  min-height: 100vh;
  background: var(--bg-primary);
}

/* Button Styles */
.btn-primary {
  background: var(--brand-primary);
  color: #000000;
  border: none;
  border-radius: 0px;
  padding: 14px 24px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
  min-height: 56px;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  line-height: 1.2;
  letter-spacing: 0;
}

.btn-primary:hover {
  background: var(--brand-hover);
  color: var(--brand-primary);
  box-shadow: 0 0 20px rgba(0, 191, 255, 0.4);
  transform: translateY(-2px);
}

.btn-primary:active {
  transform: scale(0.98);
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  border: 1px solid var(--border-subtle);
  border-radius: 0px;
  padding: 14px 24px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
  min-height: 56px;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  line-height: 1.2;
  letter-spacing: 0;
}

.btn-secondary:hover {
  background: #ffffff;
  color: #000000;
  transform: translateY(-2px);
}

.btn-secondary:active {
  transform: scale(0.98);
}

/* Typography */
.display-huge {
  font-size: 66px;
  font-weight: 600;
  line-height: 1.1;
  letter-spacing: -0.62px;
  color: var(--text-primary);
}

.display-large {
  font-size: 48px;
  font-weight: 600;
  line-height: 1.1;
  letter-spacing: -0.02em;
  color: var(--text-primary);
}

.display-medium {
  font-size: 32px;
  font-weight: 600;
  line-height: 1.25;
  letter-spacing: -0.01em;
  color: var(--text-primary);
}

.heading-1 {
  font-size: 28px;
  font-weight: 600;
  line-height: 1.2;
  letter-spacing: -0.005em;
  color: var(--text-primary);
}

.heading-2 {
  font-size: 24px;
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: -0.02em;
  color: var(--text-primary);
}

.body-large {
  font-size: 20px;
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: 0;
  color: var(--text-primary);
}

.body-medium {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0;
  color: var(--text-secondary);
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes glowPulse {
  0%, 100% {
    box-shadow: 0 0 20px rgba(0, 191, 255, 0.3);
  }
  50% {
    box-shadow: 0 0 40px rgba(0, 191, 255, 0.6);
  }
}

.fade-in-up {
  animation: fadeInUp 0.8s ease-out forwards;
}

.fade-in {
  animation: fadeIn 0.8s ease-out forwards;
}

/* Particle Background */
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

/* Responsive */
@media (max-width: 768px) {
  .display-huge {
    font-size: 42px;
  }
  
  .display-large {
    font-size: 36px;
  }
  
  .display-medium {
    font-size: 28px;
  }
  
  .btn-primary,
  .btn-secondary {
    font-size: 16px;
    padding: 12px 20px;
    min-height: 48px;
  }
}