.process-section {
  padding: 100px 7.6923%;
  background: var(--bg-primary);
  position: relative;
}

.process-container {
  max-width: 1400px;
  margin: 0 auto;
}

.process-timeline {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 40px;
  margin-top: 60px;
  position: relative;
}

.process-step {
  position: relative;
  animation: fadeInUp 0.8s ease-out forwards;
  opacity: 0;
}

.step-line {
  position: absolute;
  top: 80px;
  left: 50%;
  width: 100%;
  height: 2px;
  background: linear-gradient(to right, var(--brand-primary), transparent);
  z-index: 0;
}

.process-step:last-child .step-line {
  display: none;
}

.step-content {
  position: relative;
  z-index: 1;
  text-align: center;
  padding: 20px;
  transition: all 0.4s ease;
}

.step-content:hover {
  transform: translateY(-10px);
}

.step-number {
  font-size: 72px;
  font-weight: 700;
  color: rgba(0, 191, 255, 0.1);
  line-height: 1;
  margin-bottom: -20px;
  transition: all 0.4s ease;
}

.step-content:hover .step-number {
  color: rgba(0, 191, 255, 0.2);
  transform: scale(1.1);
}

.step-icon {
  background: rgba(0, 191, 255, 0.1);
  border: 2px solid var(--brand-primary);
  width: 80px;
  height: 80px;
  margin: 0 auto 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--brand-primary);
  position: relative;
  transition: all 0.4s ease;
}

.step-icon::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background: var(--brand-primary);
  opacity: 0;
  transition: opacity 0.4s ease;
}

.step-content:hover .step-icon {
  transform: rotate(10deg) scale(1.1);
  box-shadow: 0 10px 30px rgba(0, 191, 255, 0.4);
}

.step-content:hover .step-icon::before {
  opacity: 1;
}

.step-icon svg {
  position: relative;
  z-index: 1;
  transition: color 0.4s ease;
}

.step-content:hover .step-icon svg {
  color: #000000;
}

.step-title {
  font-size: 24px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 12px;
  letter-spacing: -0.5px;
}

.step-description {
  font-size: 16px;
  line-height: 1.6;
  color: var(--text-secondary);
}

@media (max-width: 1024px) {
  .step-line {
    display: none;
  }
}

@media (max-width: 768px) {
  .process-section {
    padding: 60px 20px;
  }
  
  .process-timeline {
    grid-template-columns: 1fr;
  }
  
  .step-number {
    font-size: 56px;
  }
}