.about-section {
  padding: 100px 7.6923%;
  background: var(--bg-primary);
  position: relative;
}

.about-container {
  max-width: 800px;
  margin: 0 auto;
}

.about-content {
  text-align: center;
  animation: fadeInUp 0.8s ease-out forwards;
  opacity: 0;
}

.about-title {
  font-size: 48px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 32px;
  letter-spacing: -1px;
}

.about-text {
  font-size: 20px;
  line-height: 1.8;
  color: var(--text-secondary);
  margin-bottom: 20px;
}

.about-text:last-child {
  margin-bottom: 0;
}

@media (max-width: 768px) {
  .about-section {
    padding: 60px 20px;
  }
  
  .about-title {
    font-size: 36px;
    margin-bottom: 24px;
  }
  
  .about-text {
    font-size: 18px;
  }
}