.success-section {
  padding: 100px 7.6923%;
  background: var(--bg-secondary);
  position: relative;
}

.success-container {
  max-width: 1400px;
  margin: 0 auto;
}

.success-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
  gap: 40px;
  margin-top: 60px;
}

.success-card {
  background: var(--bg-primary);
  border: 1px solid var(--border-subtle);
  padding: 32px;
  transition: all 0.4s ease;
  animation: fadeInUp 0.8s ease-out forwards;
  opacity: 0;
  position: relative;
  overflow: hidden;
}

.success-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 0;
  background: var(--brand-primary);
  transition: height 0.4s ease;
}

.success-card:hover::before {
  height: 100%;
}

.success-card:hover {
  transform: translateY(-8px);
  border-color: var(--brand-primary);
  box-shadow: 0 20px 60px rgba(0, 191, 255, 0.2);
}

.success-header {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 24px;
}

.success-icon {
  background: rgba(0, 191, 255, 0.1);
  border: 1px solid var(--brand-primary);
  padding: 12px;
  color: var(--brand-primary);
  transition: all 0.3s ease;
}

.success-card:hover .success-icon {
  background: var(--brand-primary);
  color: #000000;
  transform: rotate(5deg);
}

.success-company {
  font-size: 20px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 4px;
}

.success-industry {
  font-size: 14px;
  color: var(--text-muted);
}

.quote-container {
  position: relative;
  margin-bottom: 24px;
}

.quote-icon {
  position: absolute;
  top: -10px;
  left: -10px;
  color: rgba(0, 191, 255, 0.2);
  z-index: 0;
}

.success-quote {
  font-size: 16px;
  line-height: 1.7;
  color: var(--text-secondary);
  position: relative;
  z-index: 1;
  padding-left: 20px;
  font-style: italic;
}

.success-metrics {
  display: flex;
  align-items: center;
  gap: 20px;
  padding-top: 20px;
  border-top: 1px solid var(--border-subtle);
}

.metric-item {
  display: flex;
  flex-direction: column;
}

.metric-value {
  font-size: 32px;
  font-weight: 700;
  color: var(--brand-primary);
  line-height: 1;
}

.metric-label {
  font-size: 12px;
  color: var(--text-muted);
  margin-top: 4px;
}

.metric-divider {
  width: 1px;
  height: 40px;
  background: var(--border-subtle);
}

.metric-improvement {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-primary);
}

@media (max-width: 768px) {
  .success-section {
    padding: 60px 20px;
  }
  
  .success-grid {
    grid-template-columns: 1fr;
  }
  
  .success-metrics {
    flex-wrap: wrap;
  }
}