.dark-header {
  background: var(--bg-primary);
  border-bottom: 1px solid var(--border-subtle);
  padding: 16px 7.6923%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  width: 100%;
  height: 80px;
  z-index: 1000;
  box-sizing: border-box;
  transition: all 0.4s ease-in-out;
}

.dark-header.scrolled {
  background: rgba(11, 11, 11, 0.95);
  backdrop-filter: blur(12px);
  box-shadow: 0 4px 20px rgba(0, 191, 255, 0.1);
}

.header-content {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo-container {
  cursor: pointer;
  transition: transform 0.3s ease;
}

.logo-container:hover {
  transform: scale(1.05);
}

.logo-text {
  font-size: 24px;
  font-weight: 700;
  color: var(--brand-primary);
  letter-spacing: -0.5px;
}

.dark-nav {
  display: flex;
  align-items: center;
  gap: 32px;
}

.dark-nav-link {
  color: var(--text-muted);
  text-decoration: none;
  font-size: 18px;
  font-weight: 400;
  transition: color 0.3s ease;
  cursor: pointer;
  position: relative;
}

.dark-nav-link::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--brand-primary);
  transition: width 0.3s ease;
}

.dark-nav-link:hover {
  color: var(--text-primary);
}

.dark-nav-link:hover::after {
  width: 100%;
}

.mobile-menu-btn {
  display: none;
  background: none;
  border: none;
  color: var(--text-primary);
  cursor: pointer;
  padding: 8px;
  transition: transform 0.3s ease;
}

.mobile-menu-btn:hover {
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .dark-header {
    padding: 16px 20px;
    height: 70px;
  }

  .mobile-menu-btn {
    display: block;
  }

  .dark-nav {
    position: fixed;
    top: 70px;
    left: 0;
    right: 0;
    background: var(--bg-primary);
    flex-direction: column;
    padding: 20px;
    gap: 20px;
    transform: translateX(-100%);
    transition: transform 0.3s ease;
    border-bottom: 1px solid var(--border-subtle);
  }

  .dark-nav.mobile-open {
    transform: translateX(0);
  }

  .dark-nav-link {
    font-size: 20px;
  }
}