.footer {
  background: var(--bg-primary);
  border-top: 1px solid var(--border-subtle);
  padding: 60px 7.6923% 30px;
}

.footer-container {
  max-width: 1400px;
  margin: 0 auto;
}

.footer-content {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  gap: 60px;
  margin-bottom: 40px;
}

.footer-column {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.footer-logo {
  margin-bottom: 12px;
}

.logo-text {
  font-size: 24px;
  font-weight: 700;
  color: var(--brand-primary);
  letter-spacing: -0.5px;
}

.footer-description {
  font-size: 16px;
  line-height: 1.6;
  color: var(--text-secondary);
  max-width: 350px;
}

.footer-social {
  display: flex;
  gap: 12px;
  margin-top: 8px;
}

.social-link {
  background: rgba(0, 191, 255, 0.1);
  border: 1px solid var(--brand-primary);
  color: var(--brand-primary);
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  cursor: pointer;
  text-decoration: none;
}

.social-link:hover {
  background: var(--brand-primary);
  color: #000000;
  transform: translateY(-3px);
}

.footer-title {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 4px;
}

.footer-links {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.footer-link {
  font-size: 15px;
  color: var(--text-secondary);
  text-decoration: none;
  cursor: pointer;
  transition: color 0.3s ease;
}

.footer-link:hover {
  color: var(--brand-primary);
}

.contact-info {
  font-size: 15px;
  color: var(--text-secondary);
}

.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
  border-top: 1px solid var(--border-subtle);
}

.footer-copyright {
  font-size: 14px;
  color: var(--text-muted);
}

.footer-legal {
  display: flex;
  align-items: center;
  gap: 12px;
}

.legal-link {
  font-size: 14px;
  color: var(--text-muted);
  text-decoration: none;
  transition: color 0.3s ease;
}

.legal-link:hover {
  color: var(--brand-primary);
}

.legal-divider {
  color: var(--text-muted);
}

@media (max-width: 1024px) {
  .footer-content {
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }
}

@media (max-width: 768px) {
  .footer {
    padding: 40px 20px 20px;
  }
  
  .footer-content {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .footer-bottom {
    flex-direction: column;
    gap: 16px;
    text-align: center;
  }
}