.contact-section {
  padding: 100px 7.6923%;
  background: var(--bg-secondary);
  position: relative;
}

.contact-container {
  max-width: 1400px;
  margin: 0 auto;
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  align-items: center;
}

.contact-left {
  animation: fadeInUp 0.8s ease-out forwards;
}

.contact-title {
  font-size: 48px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 20px;
  line-height: 1.2;
  letter-spacing: -1px;
}

.contact-subtitle {
  font-size: 20px;
  line-height: 1.6;
  color: var(--text-secondary);
  margin-bottom: 40px;
}

.contact-features {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.contact-feature {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 18px;
  color: var(--text-primary);
}

.feature-check {
  color: var(--brand-primary);
  flex-shrink: 0;
}

.contact-right {
  animation: fadeInUp 0.8s ease-out 0.2s forwards;
  opacity: 0;
}

.contact-form {
  background: var(--bg-primary);
  border: 1px solid var(--border-subtle);
  padding: 40px;
  transition: all 0.4s ease;
}

.contact-form:hover {
  border-color: var(--brand-primary);
  box-shadow: 0 20px 60px rgba(0, 191, 255, 0.2);
}

.form-group {
  margin-bottom: 24px;
}

.form-label {
  display: block;
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.form-input {
  width: 100%;
  padding: 14px 16px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-subtle);
  color: var(--text-primary);
  font-size: 16px;
  transition: all 0.3s ease;
  font-family: inherit;
}

.form-input:focus {
  outline: none;
  border-color: var(--brand-primary);
  box-shadow: 0 0 0 3px rgba(0, 191, 255, 0.1);
}

.form-input::placeholder {
  color: var(--text-muted);
}

.form-input.error {
  border-color: #ff4444;
}

.form-textarea {
  resize: vertical;
  min-height: 120px;
}

.error-message {
  display: block;
  color: #ff4444;
  font-size: 13px;
  margin-top: 6px;
}

.submit-btn {
  width: 100%;
  margin-top: 8px;
}

.success-message {
  background: var(--bg-primary);
  border: 1px solid var(--brand-primary);
  padding: 60px 40px;
  text-align: center;
  animation: fadeIn 0.5s ease-out;
}

.success-message svg {
  color: var(--brand-primary);
  margin-bottom: 20px;
}

.success-message h3 {
  font-size: 28px;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 12px;
}

.success-message p {
  font-size: 18px;
  color: var(--text-secondary);
}

@media (max-width: 1024px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 40px;
  }
}

@media (max-width: 768px) {
  .contact-section {
    padding: 60px 20px;
  }
  
  .contact-title {
    font-size: 36px;
  }
  
  .contact-subtitle {
    font-size: 18px;
  }
  
  .contact-form {
    padding: 30px 20px;
  }
}