.hero-section {
  min-height: 100vh;
  background: var(--bg-primary);
  display: flex;
  align-items: center;
  padding: 120px 7.6923% 60px;
  position: relative;
  overflow: hidden;
}

.hero-content {
  max-width: 1400px;
  margin: 0 auto;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  align-items: center;
  z-index: 1;
}

.hero-left {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.hero-badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: rgba(0, 191, 255, 0.1);
  border: 1px solid var(--brand-primary);
  width: fit-content;
  font-size: 14px;
  color: var(--brand-primary);
  font-weight: 500;
  animation-delay: 0.2s;
}

.hero-title {
  font-size: 72px;
  font-weight: 700;
  line-height: 1.1;
  letter-spacing: -2px;
  color: var(--text-primary);
  animation-delay: 0.4s;
}

.text-gradient {
  background: linear-gradient(135deg, var(--brand-primary) 0%, #6FD2C0 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hero-subtitle {
  font-size: 20px;
  line-height: 1.6;
  color: var(--text-secondary);
  max-width: 500px;
  animation-delay: 0.6s;
}

.hero-cta {
  display: flex;
  gap: 16px;
  margin-top: 16px;
  animation-delay: 0.8s;
}

.hero-right {
  display: flex;
  justify-content: center;
  align-items: center;
}

.spline-container {
  width: 700px;
  height: 700px;
  overflow: visible;
  position: relative;
}

.hero-grid-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    repeating-linear-gradient(0deg, transparent, transparent 1px, transparent 1px, transparent 100px),
    repeating-linear-gradient(-90deg, rgba(0, 191, 255, 0.1), rgba(0, 191, 255, 0.1) 1px, transparent 1px, transparent 100px);
  opacity: 0.3;
  pointer-events: none;
  z-index: 0;
}

@media (max-width: 1024px) {
  .hero-content {
    grid-template-columns: 1fr;
    gap: 40px;
  }
  
  .hero-title {
    font-size: 56px;
  }
  
  .spline-container {
    width: 500px;
    height: 500px;
  }
}

@media (max-width: 768px) {
  .hero-section {
    padding: 100px 20px 40px;
  }
  
  .hero-title {
    font-size: 42px;
  }
  
  .hero-subtitle {
    font-size: 18px;
  }
  
  .hero-cta {
    flex-direction: column;
  }
  
  .spline-container {
    width: 100%;
    height: 400px;
  }
}